(function() {
  'use strict';

  angular
    .module('detroitstartupjobs', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ngMaterial', 'toastr', 'firebase']);

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .factory('Jobs', Jobs);

  /** @ngInject */
  function Jobs($firebaseObject) {
    var itemsRef = new Firebase(FIREBASE_URL + "/jobs");
    return $firebaseObject(itemsRef);
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .controller('JobsController', JobsController);

  /** @ngInject */
  function JobsController ($log, $mdDialog) {

    var vm = this;

    vm.goToSkill = function (chip) {
      $log.log('Clicked on Skill:');
      $log.log(chip);
    }

    vm.goToJob = function () {
      $log.log('Clicked on Job:');
    }

    vm.createJob = function (ev) {
      $mdDialog.show({
        controller: CreateDialogController,
        controllerAs: 'dialog',
        templateUrl: 'app/jobs/create.html',
        parent: angular.element(document.body),
        targetEvent: ev,
        clickOutsideToClose: true
      });
    }

    vm.jobs = [
      {
        "title": "Campus Director",
        "created_on": "Friday",
        "salary": 88000,
        "employer": "The Iron Yard",
        "location": "Detroit",
        "skills": ["JavaScript", "iOS", "Android", "Adobe Illustrator"],
        "description": "A position opening The Iron Yard location in Detroit, and working with students and community partners."
      },
      {
        "title": "Post Production Coordinator",
        "created_on": "Wed",
        "salary": 30000,
        "employer": "Heather Read Photography & Design",
        "location": "Royal Oak",
        "skills": ["Adobe Photoshop"],
        "description": "You will be responsible for editing Heather's photos during post-production, and working with Adobe Photoshop actions."
      },
      {
        "title": "Junior Front End Developer",
        "created_on": "Wed",
        "salary": 45000,
        "employer": "Gas Station TV",
        "location": "Detroit",
        "skills": ["JavaScript", "HTML", "CSS"],
        "description": "A great position to grow as into a robust front-end developer."
      }
    ]
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .controller('CreateDialogController', CreateDialogController);

  /** @ngInject */
  function CreateDialogController ($log, $mdDialog) {

    var vm = this;

    vm.cancel = function () {
      $mdDialog.cancel();
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('jobs', {
        url: '/jobs',
        templateUrl: 'app/jobs/index.html',
        controller: 'JobsController',
        controllerAs: 'home'
      });

    $urlRouterProvider.otherwise('/jobs');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .constant('malarkey', malarkey)
    .constant('moment', moment)
    .constant('FIREBASE_URL', 'https://detroitstartupjobs.firebaseio.com');

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig, $mdThemingProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;

    // set our material design theme
    $mdThemingProvider.theme('default')
     .primaryPalette('cyan')
     .accentPalette('teal')
     .backgroundPalette('grey');
  }

})();

